// import fonts/font <=>
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700;800;900&display=swap');
// default font / default font size <=>
$fontFamily: 'Montserrat', sans-serif;
$fontSize: 14;
//transition for dark mode and light
* {
   transition: color 0.2s;
   transition: background 0.2s;
}
// colors palette
:root {
   --black-p: #151515;
   --black-s: #181818;
   --black-t: #202020;
   --white-p: #ffffff;
   --white-s: #cccccc;
   --main: #23d21f;
   --main-l: #22d21fd7;
   --border: #292929;
   // game colors
   --green: #72ff30;
   --blue: #3072ff;
}
.darkmode {
   --black-p: #fdf2f0;
   --black-s: #f3e9e7;
   --white-p: #111113;
   --white-s: #333333;
   --main: #21c81e;
   --main-l: #1da51b;
   --border: #292929;
   // game colors
   --green: #60db27;
   --blue: #1d4eb9;
}
// max width for container <=>
$maxWidthContainer: 1250;
// size gadjes for adapted and other <=>
$tablet: 991.98px;
$mobile: 767.98px;
$mobileSmall: 479.98px;
// null style <=>
@import 'scss/_null.scss';
// general style <=>
.root,
body,
.app {
   height: 100svh;
}
body {
   line-height: 1.4;
   font-family: $fontFamily;
   font-size: $fontSize + px;
   background: var(--black-p);
   animation: mainanim 0.7s ease-in-out;
   color: var(--white-p);
}
@keyframes mainanim {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}
// wrapper <=>
.root {
   min-height: 100%;
   display: flex;
   flex-direction: column;
   overflow: hidden;
}
.container {
   max-width: $maxWidthContainer + px;
   margin: 0 auto;
   //fix for slider
   > * {
      min-width: 0;
   }
}
.main {
   padding: 80px 15px 15px 15px;
   max-width: $maxWidthContainer + px;
   margin: 0 auto;
   @media (max-width: $mobile) {
      padding: 75px 15px 80px 15px;
   }
}

// scroll bar <=>
@media only screen and (max-width: $mobile) {
   ::-webkit-scrollbar {
      display: none;
   }
}
::-webkit-scrollbar {
   background-color: var(--black-s);
   width: 16px;
}
::-webkit-scrollbar-track {
   background-color: var(--black-s);
}
::-webkit-scrollbar-thumb {
   background-color: var(--white-p);
   border-radius: 16px;
   border: 4px solid var(--black-s);
}
::-webkit-scrollbar-thumb:hover {
   background-color: var(--white-s);
}
::-webkit-scrollbar-thumb:active {
   background-color: var(--white-p);
}
::-webkit-scrollbar-button {
   display: none;
}

// when copu text<=>
::selection {
   color: #101010;
   background: var(--yellow);
}

// imports
@import './scss/_utils';
@import './scss/_header';
@import './scss/_footer';
@import './scss/_settings';
@import './scss/_zeroandone';
@import './scss/_game-link';
@import './scss/_intro';
