.games {
   padding-bottom: 15px;
   display: flex;
   flex-direction: column;
   gap: 10px;
   &__grid {
      display: grid;
      gap: 10px;
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      @media (max-width: $mobile) {
         grid-template-columns: repeat(1, 1fr);
      }
   }
}
.game-link {
   border-radius: 20px;
   position: relative;
   overflow: hidden;
   width: 100%;
   &__left {
      display: flex;
      flex-direction: column;
      gap: 15px;
      padding: 10px;
      padding-right: 30%;
   }
   &__title {
      padding: 10px;
      font-size: 18px;
      z-index: 10;
      color: #080808;
   }
   &__link {
      padding: 5px 20px;
      background: #ffffff80;
      border-radius: 20px;
      display: inline-block;
      color: #080808;
      font-weight: 600;
   }
   &__img {
      position: absolute;
      height: 100%;
      right: 0;
      top: 0;
      z-index: 0;
      aspect-ratio: 1/1;
   }
}
