.settings {
   user-select: none;
   &__body {
      display: grid;
      gap: 15px;
      grid-template-rows: repeat(1, 1fr);
      grid-template-columns: 0.8fr 1.2fr;
      @media (max-width: $tablet) {
         grid-template-rows: repeat(1, 1fr);
         grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: $mobile) {
         grid-template-rows: auto auto;
         grid-template-columns: repeat(1, 1fr);
      }
   }
   &__intro {
      background-image: radial-gradient(
            100% 100% at 30% 100%,
            #feb2b2 0%,
            rgba(252, 184, 219, 0.75) 25%,
            rgba(239, 194, 250, 0.5) 50%,
            rgba(190, 227, 248, 0) 100%
         ),
         linear-gradient(
            90deg,
            #3182ce 0%,
            #348fc5 14.29%,
            #389ac2 28.57%,
            #3ca5c1 42.86%,
            #41b0c2 57.14%,
            #45bbc3 71.43%,
            #4fd1c5 100%
         );
      transition: 0.2s;
      cursor: pointer;
      height: calc(100svh - 95px);
      border-radius: 30px;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      padding: 30px;
      &:hover {
         filter: contrast(150%);
      }
      &:active {
         transform: scale(0.98);
      }
      @media (max-width: $mobile) {
         height: auto;
         padding: 25px;
         aspect-ratio: 5/3;
      }
   }
   &__title {
      font-size: 30px;
      font-weight: 700;
      line-height: 1;
      color: #151515;
      @media (max-width: $mobileSmall) {
         font-size: 25px;
      }
   }
   &__text {
      font-weight: 600;
      color: #151515;
   }
   &__list {
      display: flex;
      flex-direction: column;
      gap: 15px;
   }
}
// switch mode light / dark
.setting {
   padding: 15px 15px 15px 30px;
   background: var(--black-s);
   display: flex;
   border-radius: 30px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   transition: 0.1s;

   &__name {
      color: var(--white-p);
      font-weight: 600;
   }
   &__toggle {
      width: 50px;
      height: 30px;
      background: var(--white-s);
      border-radius: 20px;
      position: relative;
      transition: 0.1s;
      &::after {
         content: '';
         position: absolute;
         top: 0;
         left: 0;
         width: 20px;
         height: 20px;
         border-radius: 20px;
         inset: 5px;
         background: var(--black-p);
         transition: 0.1s;
      }
   }
   &.active &__toggle {
      background: var(--main);
      &::after {
         left: 25px;
      }
   }
}
// switch mode playmusic of stop
.setting {
   &.play &__toggle {
      background: var(--main);
      &::after {
         left: 25px;
      }
      &:active {
         transform: scale(0.95);
      }
   }
   &.sound &__toggle {
      background: var(--main);
      &::after {
         left: 25px;
      }
      &:active {
         transform: scale(0.95);
      }
   }
}
.setting {
   &__bar {
      height: 30px;
      width: auto;
      gap: 3px;
      display: flex;
      border-radius: 30px;
   }
   &__bar-item {
      background: var(--white-s);
      width: 40px;
      height: 30px;
      position: relative;
      transition: 0.2s;
      cursor: pointer;
      border-radius: 3px;
      &:first-child {
         border-top-left-radius: 20px;
         border-bottom-left-radius: 20px;
      }
      &:last-child {
         border-top-right-radius: 20px;
         border-bottom-right-radius: 20px;
      }
   }
   &__bar-item.active {
      background: var(--main);
   }
}
