.intro {
   padding-top: 15px;
   user-select: none;
   &__body {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 15px;
      @media (max-width: $mobile) {
         grid-template-columns: 1fr;
         grid-template-rows: 1fr 1fr;
      }
   }
   &__info {
      display: flex;
      flex-direction: column;
      padding: 30px;
      border-radius: 30px;
      position: relative;
      aspect-ratio: 2 / 1;
      cursor: pointer;
      background-image: radial-gradient(
            100% 100% at 0% 50%,
            #7bd5d1 0%,
            rgba(113, 169, 226, 0.67) 33.33%,
            rgba(177, 56, 94, 0) 100%
         ),
         linear-gradient(174deg, #ffed7a 0%, #c1ed5a 51%, #39cc1e 100%);
      background-position: center;
      transition: 0.2s;
      @media (max-width: $tablet) {
         padding: 20px;
      }
      @media (max-width: $mobile) {
         padding: 25px;
         aspect-ratio: 5 / 3;
      }
      &:hover {
         filter: contrast(200%);
      }

      &:hover {
         background-size: 150%;
      }
      &:active {
         transform: scale(0.98);
      }
   }

   &__text {
      z-index: 2;
      color: #151515;
      font-weight: 600;
      padding-right: 40px;
      width: 70%;

      @media (max-width: $mobile) {
         font-size: 12px;
         width: 100%;
      }
   }
   &__logo {
      flex: 1 1 auto;
      img {
         width: 80%;
         object-fit: contain;
      }
   }
   &__logo-mini {
      img {
         position: absolute;
         bottom: 30px;
         right: 30px;
         height: 30px;
         width: auto;
         mix-blend-mode: darken;
         @media (max-width: $tablet) {
            bottom: 20px;
            right: 20px;
         }
         @media (max-width: $mobile) {
            display: none;
         }
      }
   }
}
.intro-d {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   padding: 30px;
   border-radius: 30px;
   position: relative;
   aspect-ratio: 2 / 1;
   overflow: hidden;
   filter: contrast(100%);
   transition: 0.2s;
   @media (max-width: $tablet) {
      padding: 20px;
   }
   @media (max-width: $mobile) {
      padding: 25px;
      aspect-ratio: 5 / 3;
   }
   &:hover {
      filter: contrast(200%);
   }
   &:active {
      transform: scale(0.98);
   }
   &__img-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
   &:hover &__img-bg {
      transform: scale(1.05);
   }
   &__logo {
      z-index: 2;
      img {
         width: 25%;
         object-fit: contain;
      }
   }
   &__text {
      z-index: 2;
      color: #151515;
      font-weight: 600;
      padding-right: 40px;
      @media (max-width: $mobile) {
         font-size: 12px;
         width: 100%;
      }
      span {
         text-decoration-line: underline;
      }
   }
   &__link {
      position: absolute;
      height: 18px;
      width: 18px;
      bottom: 30px;
      right: 30px;
      color: #151515;
   }
}
