.game {
   text-align: center;
   display: flex;
   height: 100%;
   gap: 20px;
   user-select: none;
   height: calc(100svh - 155px);
   @media (max-width: $mobile) {
      flex-direction: column;
      justify-content: space-between;
   }
   &__info {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      gap: 10px;
      flex: 1 1 auto;
   }
   &__info-top {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 10px;
      width: 100%;
      padding: 20px;
      @media (max-width: $mobile) {
         align-items: center;
      }
   }
   &__title {
      font-size: 30px;
      font-weight: 600;
   }
   &__text {
      font-size: 18px;
      color: var(--white-p);
      transition: 0.3s;
   }
   &__text.player1 {
      color: var(--green);
   }
   &__text.player0 {
      color: var(--blue);
   }
   &__btn {
      background: var(--black-s);
      color: var(--white-p);
      font-weight: 600;
      width: 100%;
      border-radius: 30px;
      padding: 15px;
      transition: 0.3s;
   }
   &__cell-container {
      aspect-ratio: 1 / 1;
      height: 100%;
      width: auto;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 1fr);
      gap: 15px;
      @media (max-width: $mobile) {
         height: 50%;
         margin: 0 auto;
      }
   }
   &__cell {
      aspect-ratio: 1/1;
      display: flex;
      justify-content: center;
      align-items: center;
      background: var(--black-s);
      font-size: 50px;
      font-weight: 600;
      border-radius: 30px;
      cursor: pointer;
      color: var(--black-s);
      transition: 0.3s;
      @media (max-width: $mobile) {
         font-size: 30px;
      }
   }
   &__cell.cell1 {
      color: var(--green);
   }
   &__cell.cell0 {
      color: var(--blue);
   }
   &.game-over {
      .game__cell.winning {
         background: var(--black-t);
         color: white;
      }
      .game__cell.not-winning {
         color: var(--black-p);
      }
      .game__cell {
         cursor: no-drop;
      }
      .game__btn {
         color: var(--black-p);
         background: var(--black-t);
      }
      .game__text.player1 {
         color: var(--white-p);
      }
      .game__text.player0 {
         color: var(--white-p);
      }
   }
}
