.header {
   position: fixed;
   background: var(--black-s);
   width: 100%;
   z-index: 100;
   &__body {
      width: 100%;
      min-height: 30px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
      padding: 15px;
   }
   &__logo {
      width: auto;
      height: 30px;
      position: relative;
   }
}

.menu {
   &__list {
      display: flex;
      gap: 30px;
      user-select: none;
   }
   &__link {
      > div {
         width: 70px;
         height: 35px;
         display: flex;
         align-items: center;
         justify-content: center;
         background: transparent;
         transition: 0.3s;
         position: relative;
         &::after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            height: 35px;
            width: 35px;
            border-radius: 30px;
            transition: 0.2s ease;
            z-index: 1;
         }
         > div {
            display: flex;
            align-items: center;
            justify-content: center;
            z-index: 2;
         }
      }
      svg {
         width: 25px;
         height: 25px;
         color: var(--white-s);
         transition: 0.2s;
      }
      &.active svg {
         color: var(--black-p);
      }
      &:hover svg {
         @media (min-width: $mobile) {
            color: var(--black-s);
         }
      }

      &.active > div::after {
         background: var(--main);
         width: 70px;
      }
      &:hover > div::after {
         @media (min-width: $mobile) {
            background: var(--main-l);
            width: 70px;
         }
      }
   }
}
@media (max-width: $mobile) {
   .header {
      pointer-events: none;
   }
   .menu {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100svh;
      display: flex;
      justify-content: flex-end;
      flex-direction: column;
      &__list {
         pointer-events: all;
         display: flex;
         gap: 30px;
         width: 100%;
         min-height: 30px;
         padding: 15px;
         background: var(--black-s);
         display: flex;
         align-items: center;
         justify-content: space-around;
      }
   }
}
